import { getFontsNinjaUrlByEnvironment } from '../data/FontsNinja';
export const buildFontsNinjaUrl = (machineId) => {
    let url = getFontsNinjaUrlByEnvironment();
    if (machineId) {
        url += `?machineId=${machineId}`;
    }
    return url;
};
export const buildSinglePageFontUrl = (data, idRequest, machineId, currentColor) => {
    let singlePageFontUrl = `${getFontsNinjaUrlByEnvironment()}${data.url}?extension`;
    if (idRequest) {
        singlePageFontUrl += `&idRequest=${idRequest}`;
    }
    if (machineId) {
        singlePageFontUrl += `&machineId=${machineId}`;
    }
    if (data.idFontVersion) {
        singlePageFontUrl += `&idFontVersion=${data.idFontVersion}`;
    }
    if (currentColor) {
        const color = encodeURIComponent(JSON.stringify(currentColor));
        singlePageFontUrl += `&color=${color}`;
    }
    return singlePageFontUrl;
};
export const buildBookmarksUrl = (machineId) => {
    let bookmarksUrl = `${getFontsNinjaUrlByEnvironment()}/bookmarks`;
    if (machineId) {
        bookmarksUrl += `?machineId=${machineId}`;
    }
    return bookmarksUrl;
};
export const buildOverviewUrl = () => {
    const overviewUrl = `${getFontsNinjaUrlByEnvironment()}/overview`;
    return overviewUrl;
};
