import { useCallback, useEffect, useState } from 'react';
export const useAppState = (data) => {
    const [isOpen, setIsOpen] = useState(data.isOpen);
    const [extensionPath] = useState(data.extensionPath);
    const [openSettings, setOpenSettings] = useState(data.openSettings);
    const [openIframe, setOpenIframe] = useState(data.openIframe);
    const getExtensionOpenOrClose = useCallback((event) => {
        const detail = JSON.parse(event.detail);
        setIsOpen(detail.isOpen);
    }, []);
    useEffect(() => {
        document.addEventListener('fontsninja-ext-opened', getExtensionOpenOrClose);
        return () => {
            document.removeEventListener('fontsninja-ext-opened', getExtensionOpenOrClose);
        };
    }, [getExtensionOpenOrClose]);
    return {
        isOpen,
        extensionPath,
        openSettings,
        setOpenSettings,
        openIframe,
        setOpenIframe,
    };
};
