const PROD_FONTS_NINJA_URL = 'https://fonts.ninja';
const PREPROD_FONTS_NINJA_URL = 'https://v2-preprod.fonts.ninja';
const DEV_FONTS_NINJA_URL = 'http://localhost:3000';
export const getFontsNinjaUrlByEnvironment = () => {
    switch (process.env.NODE_ENV) {
        case 'development':
            return DEV_FONTS_NINJA_URL;
        case 'preprod':
            return PREPROD_FONTS_NINJA_URL;
        default:
            return PROD_FONTS_NINJA_URL;
    }
};
