import { useCallback, useEffect, useRef, useState } from 'react';
import { INJECT_FONTS_NINJA_HELPERS, NINJA_PROCESS_RESULTS } from 'src/common/data';
import { isEmpty } from 'src/common/utils';
export const useFontFamilyState = (data) => {
    const mounted = useRef(false);
    const [idRequest, setIdRequest] = useState(null);
    const [fontFamilies, setFontFamilies] = useState(data);
    const [fontFamiliesToDisplay, setFontFamiliesToDisplay] = useState(data);
    const [selectedFontFamily, setSelectedFontFamily] = useState(null);
    const [hoverSubFontFamily, setHoverSubFontFamily] = useState(null);
    const getFontFamilies = useCallback(({ data }) => {
        if (data.type === NINJA_PROCESS_RESULTS) {
            setIdRequest(data.idRequest);
            setFontFamilies(data.results);
            getFontFamiliesToDisplay(data.results);
        }
    }, []);
    const getFontFamiliesToDisplay = (fontFamiliesData) => {
        const fontFamiliesToDisplay = {};
        const fontFamiliesName = Object.keys(fontFamiliesData);
        fontFamiliesName.forEach((fontFamilyName) => {
            if (fontFamiliesData[fontFamilyName][0].detectionMode !== 'local') {
                fontFamiliesToDisplay[fontFamilyName] = fontFamiliesData[fontFamilyName];
            }
        });
        if (!isEmpty(fontFamiliesToDisplay)) {
            setFontFamiliesToDisplay(fontFamiliesToDisplay);
        }
        else {
            setFontFamiliesToDisplay(fontFamiliesData);
        }
    };
    useEffect(() => {
        window.addEventListener('message', getFontFamilies);
        window.postMessage({ type: INJECT_FONTS_NINJA_HELPERS });
        mounted.current = true;
        return () => {
            window.removeEventListener('message', getFontFamilies);
            mounted.current = false;
        };
    }, [getFontFamilies]);
    return {
        idRequest,
        fontFamilies,
        fontFamiliesToDisplay,
        selectedFontFamily,
        setSelectedFontFamily,
        hoverSubFontFamily,
        setHoverSubFontFamily,
    };
};
