import { useState } from 'react';
;
export const useBaseState = (data) => {
    const [dragStatus, setDragStatus] = useState(data.dragStatus);
    const [pushMediaReady, setPushMediaReady] = useState(data.pushMediaReady);
    const [transitionPushToFonts, setTransitionPushToFonts] = useState(data.pushMediaReady);
    return {
        dragStatus,
        setDragStatus,
        pushMediaReady,
        setPushMediaReady,
        transitionPushToFonts,
        setTransitionPushToFonts,
    };
};
