import styled from '@emotion/styled';
import { css } from '@emotion/react';
import { GREY_2, GREY_6, WHITE } from 'design-system/global/colors';
const borderDarkMode = `1px solid ${GREY_2}`;
const borderLightMode = `1px solid ${GREY_6}`;
export const HeaderStyled = styled.div `
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  border-top: ${(props) => (props.darkMode ? borderDarkMode : borderLightMode)};
  border-bottom: ${(props) => (props.darkMode ? borderDarkMode : borderLightMode)};
  margin-top: -1px;
  box-sizing: border-box;
`;
export const BookmarksWrapper = styled.div `
  display: flex;
  flex: 1;
  height: 100%;
`;
const iconHoverEffect = (props) => css `
  background-color: ${props.darkMode ? WHITE : GREY_6};
  i {
    color: ${props.darkMode ? GREY_6 : WHITE};
  }
`;
export const IconButtonWrapper = styled.div `
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 100%;
  background-color: ${(props) => (props.darkMode ? GREY_6 : WHITE)};
  border-left: ${(props) => (props.darkMode ? borderDarkMode : borderLightMode)};
  box-sizing: border-box;
  i {
    color: ${(props) => (props.darkMode ? WHITE : GREY_6)};
  }

  &.hover-effect {
    ${(props) => iconHoverEffect(props)}
  }

  @media (hover: hover) {
    &:hover {
      ${(props) => iconHoverEffect(props)}
    }
  }
`;
