import { useCallback, useEffect, useRef, useState } from 'react';
import { AUTH, GET_MACHINE_ID, GET_STORAGE, INSTAGRAM_PUSH_NOTIF, SAVE_STORAGE, SEND_MACHINE_ID, SEND_STORAGE, SETTINGS, } from 'src/common/data';
export const useSettingsState = (data) => {
    const mounted = useRef(false);
    const settingsRef = useRef(data);
    const incremented = useRef(false);
    const themeRef = useRef(data.theme);
    const [settingsReady, setSettingsReady] = useState(false);
    const [checkedDisplayPush, setCheckedDisplayPush] = useState(false);
    const [machineId, setMachineId] = useState(null);
    const [settings, setSettings] = useState(data);
    const getMachineId = useCallback(() => {
        window.addEventListener('message', ({ data }) => {
            if (data && data.type === SEND_MACHINE_ID) {
                setMachineId(data.machineId);
            }
        });
        window.postMessage({ type: GET_MACHINE_ID, key: AUTH });
    }, []);
    const getSettingsStorage = useCallback(() => {
        window.addEventListener('message', ({ data }) => {
            if (data && data.type === SEND_STORAGE) {
                const settingsStorage = data.storage;
                if (settingsStorage) {
                    const newSettings = Object.assign({}, settings);
                    for (const key of Object.keys(settingsStorage)) {
                        if (key in newSettings) {
                            newSettings[key] = settingsStorage[key];
                        }
                    }
                    newSettings['darkMode'] = getDarkModeValue(newSettings.theme);
                    setSettings(newSettings);
                    settingsRef.current = newSettings;
                    themeRef.current = newSettings.theme;
                }
                else {
                    settings.darkMode = getDarkModeValue(settings.theme);
                }
                setSettingsReady(true);
            }
        });
        window.postMessage({ type: GET_STORAGE, key: SETTINGS });
    }, [settings]);
    const updateSettingsElement = useCallback((key, value, saveToChromeStorage = true) => {
        const newSettings = Object.assign({}, settingsRef.current);
        newSettings[key] = value;
        settingsRef.current = newSettings;
        setSettings(newSettings);
        if (saveToChromeStorage) {
            // Update chrome storage settings
            window.postMessage({
                type: SAVE_STORAGE,
                key: SETTINGS,
                attributes: [key],
                values: [value],
            });
        }
    }, []);
    const updateTheme = useCallback((value) => {
        const newSettings = Object.assign({}, settingsRef.current);
        newSettings['theme'] = value;
        newSettings['darkMode'] = getDarkModeValue(newSettings.theme);
        setSettings(newSettings);
        settingsRef.current = newSettings;
        themeRef.current = newSettings.theme;
        // Update chrome storage settings
        window.postMessage({
            type: SAVE_STORAGE,
            key: SETTINGS,
            attributes: ['theme', 'darkMode'],
            values: [newSettings.theme, newSettings.darkMode],
        });
    }, []);
    const checkInstagramPush = useCallback(() => {
        if (INSTAGRAM_PUSH_NOTIF.includes(settings.launchCount) && !settings.instaSeen) {
            settings.instaForce = true;
        }
        // TODO: passer à true après avoir requeter les custom push aussi
        // TODO: forcer à true si requete custom push est trop longue à répondre
        setCheckedDisplayPush(true);
    }, [settings]);
    const incrementLaunchCount = useCallback(() => {
        incremented.current = true;
        settings.launchCount += 1;
        checkInstagramPush();
        // Update chrome storage settings
        window.postMessage({
            type: SAVE_STORAGE,
            key: SETTINGS,
            attributes: ['launchCount'],
            values: [settings.launchCount],
        });
    }, [checkInstagramPush, settings]);
    const getDarkModeValue = (theme) => {
        switch (theme) {
            case 'auto':
                return window.matchMedia ? window.matchMedia('(prefers-color-scheme: dark)').matches : false;
            case 'light':
                return false;
            case 'dark':
                return true;
        }
    };
    useEffect(() => {
        if (settingsReady && !incremented.current) {
            incrementLaunchCount();
        }
    }, [incrementLaunchCount, settingsReady]);
    useEffect(() => {
        mounted.current = true;
        return () => {
            mounted.current = false;
        };
    }, []);
    if (!mounted.current) {
        getMachineId();
        getSettingsStorage();
        window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', (event) => {
            if (themeRef.current === 'auto') {
                updateSettingsElement('darkMode', event.matches);
            }
        });
    }
    return {
        settingsReady,
        checkedDisplayPush,
        machineId,
        settings,
        setSettings,
        updateSettingsElement,
        updateTheme,
    };
};
