import styled from '@emotion/styled';
import { css } from '@emotion/react';
import { GREY_2, GREY_6, WHITE } from 'design-system/global/colors';
const TOOLTIP_MIN_WIDTH = '286px';
const borderDarkMode = `1px solid ${GREY_2}`;
const borderLightMode = `1px solid ${GREY_6}`;
export const TooltipStyled = styled.div `
  display: ${(props) => props.display};
  position: fixed;
  top: -999px;
  left: -999px;
  min-width: ${TOOLTIP_MIN_WIDTH};
  max-width: 340px;
  height: 94px;
  background-color: ${(props) => (props.darkMode ? GREY_6 : WHITE)};
  border: ${(props) => (props.darkMode ? borderDarkMode : borderLightMode)};
  box-shadow: ${(props) => (props.darkMode ? 'none' : `4px 4px 0px ${GREY_6}`)};
  border-radius: 10px;
  box-sizing: border-box;
  overflow: hidden;
  z-index: 2147483647;
  ${(props) => props.isTouchDevice &&
    css `
      pointer-events: auto;
    `}
`;
