import styled from '@emotion/styled';
import { css } from '@emotion/react';
import { GREY_2, GREY_6, WHITE } from 'design-system/global/colors';
const borderWhite = `1px solid ${WHITE}`;
const borderDarkMode = `1px solid ${GREY_2}`;
const borderLightMode = `1px solid ${GREY_6}`;
export const FontFamilyItemWrapper = styled.div `
  display: flex;
  align-items: center;
  padding: 16px 16px 16px 10px;
  background-color: ${(props) => (props.darkMode ? GREY_6 : WHITE)};
  border-bottom: ${(props) => (props.darkMode ? borderDarkMode : borderLightMode)};
  box-sizing: border-box;
  &:last-of-type {
    border-bottom: none;
  }
`;
export const FontFamilyContainer = styled.div `
  display: inline-flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
`;
const subFontFamilyHoverEffect = () => css `
  .icon-hover {
    visibility: visible;
  }
`;
export const SubFontFamilyWrapper = styled.div `
  position: relative;
  display: flex;
  min-height: 20px;
  align-items: center;
  cursor: ${(props) => (props.detectionMode === 'identify' ? 'pointer' : 'default')};

  &.hover-effect {
    ${subFontFamilyHoverEffect()}
  }

  @media (hover: hover) {
    &:hover {
      ${subFontFamilyHoverEffect()}
    }
  }
`;
export const ArrowHover = styled.img `
  width: 10px;
  height: 8px;
  margin-right: 5px;
  visibility: hidden;
`;
export const SubFontFamilyName = styled.p `
  display: inline;
  font-size: 16px;
  color: ${(props) => (props.darkMode ? WHITE : GREY_6)};
  font-family: ${(props) => props.fontFamily};
  font-style: ${(props) => props.fontStyle};
  font-weight: ${(props) => props.fontWeight};
  padding-right: 14px;
  text-overflow: ${(props) => (props.textOverflow ? 'ellipsis' : 'clip')};
  white-space: nowrap;
  overflow: hidden;
`;
export const SubFontFamilyCartouche = styled.span `
  display: inline-flex;
  align-items: center;
  height: 19px;
  padding: 0 6px;
  border: ${(props) => (props.darkMode ? borderWhite : borderLightMode)};
  border-radius: 51px;
  box-sizing: border-box;
`;
