export const getStyles = (element) => {
    const rawStyle = window.getComputedStyle(element);
    const style = {};
    for (let i = 0; i < rawStyle.length; i++) {
        const snakeProp = rawStyle.item(i);
        const camelProp = snakeProp.replace(/-(.)/g, ([, c]) => c.toUpperCase());
        style[camelProp] = rawStyle[snakeProp];
    }
    return style;
};
