import styled from '@emotion/styled';
import { css } from '@emotion/react';
import { GREY_6, WHITE } from 'design-system/global/colors';
const hoverEffectStyles = (props) => css `
  background-color: ${props.darkMode ? WHITE : GREY_6};

  [class^='icon-'],
  [class*=' icon-'] {
    color: ${props.darkMode ? GREY_6 : WHITE};
  }

  .bookmarks-label {
    color: ${props.darkMode ? GREY_6 : WHITE};
  }
`;
export const Link = styled.a `
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 17px 0 24px;
  text-decoration: none;
  background-color: ${(props) => (props.darkMode ? GREY_6 : WHITE)};

  [class^='icon-'],
  [class*=' icon-'] {
    color: ${(props) => (props.darkMode ? WHITE : GREY_6)};
  }

  .bookmarks-label {
    color: ${(props) => (props.darkMode ? WHITE : GREY_6)};
    margin-left: 8px;
    font-weight: 700;
  }

  &.hover-effect {
    ${(props) => hoverEffectStyles(props)}
  }

  @media (hover: hover) {
    &:hover {
      ${(props) => hoverEffectStyles(props)}
    }
  }
`;
